import { notFound } from 'next/navigation';
import Link from 'next/link';
import { Suspense } from 'react';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import rehypeHighlight from 'rehype-highlight';
import 'highlight.js/styles/github-dark.css';

interface BlogPost {
  title: string;
  date: string;
  path: string;
  category: string;
  content: string;
}

interface PageProps {
  searchParams: Promise<{ path?: string }>;
}

async function fetchBlogPost(path: string): Promise<BlogPost | null> {
  try {
    const response = await fetch(`http://localhost:8080/api/blog-posts/${encodeURIComponent(path)}`, {
      cache: 'no-cache',
      next: { revalidate: 3600 }
    });
    
    if (!response.ok) {
      return null;
    }
    
    return await response.json();
  } catch (error) {
    console.error('Erreur lors du chargement de l\'article:', error);
    return null;
  }
}

function MarkdownRenderer({ content }: { content: string }) {
  const renderMarkdown = (text: string) => {
    // D'abord, traiter les blocs de code pour les protéger
    const codeBlocks: string[] = [];
    let processedText = text.replace(/```(\w+)?\n([\s\S]*?)```/g, (match, lang, code) => {
      const index = codeBlocks.length;
      const cleanCode = code.trim();
      codeBlocks.push(`<pre class="bg-gray-900 text-gray-100 p-4 rounded-lg overflow-x-auto my-6 border"><code class="text-sm font-mono whitespace-pre language-${lang || 'text'}">${cleanCode.replace(/</g, '&lt;').replace(/>/g, '&gt;')}</code></pre>`);
      return `__CODE_BLOCK_${index}__`;
    });

    // Traiter le code inline
    processedText = processedText.replace(/`([^`]+)`/g, '<code class="bg-foreground/10 px-2 py-1 rounded text-sm font-mono text-foreground">$1</code>');

    // Traiter les titres
    processedText = processedText.replace(/^### (.*$)/gm, '<h3 class="text-xl font-semibold mt-8 mb-4 text-foreground border-b border-foreground/10 pb-2">$1</h3>');
    processedText = processedText.replace(/^## (.*$)/gm, '<h2 class="text-2xl font-semibold mt-10 mb-6 text-foreground border-b border-foreground/20 pb-2">$1</h2>');
    processedText = processedText.replace(/^# (.*$)/gm, '<h1 class="text-3xl font-bold mt-12 mb-8 text-foreground border-b border-foreground/30 pb-3">$1</h1>');

    // Traiter les images
    processedText = processedText.replace(/!\[([^\]]*)\]\(([^)]+)\)/g, '<img src="$2" alt="$1" class="max-w-full h-auto rounded-lg my-6 border border-foreground/10" />');
    
    // Traiter les liens
    processedText = processedText.replace(/\[([^\]]+)\]\(([^)]+)\)/g, '<a href="$2" target="_blank" class="text-blue-600 dark:text-blue-400 underline decoration-1 underline-offset-4 hover:decoration-2 hover:text-blue-800 dark:hover:text-blue-300 transition-colors">$1</a>');

    // Traiter le texte en gras
    processedText = processedText.replace(/\*\*([^*]+)\*\*/g, '<strong class="font-bold text-foreground">$1</strong>');

    // Traiter le texte en italique
    processedText = processedText.replace(/\*([^*]+)\*/g, '<em class="italic text-foreground/90">$1</em>');

    // Traiter les listes
    const lines = processedText.split('\n');
    const processedLines: string[] = [];
    let inList = false;

    for (let i = 0; i < lines.length; i++) {
      const line = lines[i];
      const isListItem = line.match(/^[\s]*-\s+(.+)$/);
      
      if (isListItem) {
        if (!inList) {
          processedLines.push('<ul class="list-disc ml-6 my-4 space-y-2">');
          inList = true;
        }
        processedLines.push(`<li class="text-foreground/90 leading-relaxed">${isListItem[1]}</li>`);
      } else {
        if (inList) {
          processedLines.push('</ul>');
          inList = false;
        }
        processedLines.push(line);
      }
    }

    if (inList) {
      processedLines.push('</ul>');
    }

    processedText = processedLines.join('\n');

    // Traiter les paragraphes
    const paragraphs = processedText.split('\n\n').filter(p => p.trim() !== '');
    const processedParagraphs = paragraphs.map(paragraph => {
      const trimmed = paragraph.trim();
      
      // Ignorer les éléments HTML déjà traités
      if (trimmed.startsWith('<') || trimmed.includes('__CODE_BLOCK_')) {
        return trimmed;
      }
      
      // Traiter comme paragraphe normal
      const lines = trimmed.split('\n').filter(line => line.trim() !== '');
      if (lines.length === 1) {
        return `<p class="mb-4 leading-relaxed text-foreground/90">${lines[0]}</p>`;
      } else {
        return `<p class="mb-4 leading-relaxed text-foreground/90">${lines.join('<br>')}</p>`;
      }
    });

    let finalText = processedParagraphs.join('\n\n');

    // Restaurer les blocs de code
    codeBlocks.forEach((block, index) => {
      finalText = finalText.replace(`__CODE_BLOCK_${index}__`, block);
    });

    return finalText;
  };

  return (
    <div 
      className="prose prose-lg max-w-none"
      dangerouslySetInnerHTML={{ 
        __html: renderMarkdown(content)
      }}
    />
  );
}

function LoadingSkeleton() {
  return (
    <div className="animate-pulse">
      <div className="h-8 bg-foreground/10 rounded w-3/4 mb-4"></div>
      <div className="flex gap-4 mb-6">
        <div className="h-4 bg-foreground/10 rounded w-20"></div>
        <div className="h-4 bg-foreground/10 rounded w-24"></div>
      </div>
      <div className="space-y-3">
        <div className="h-4 bg-foreground/10 rounded w-full"></div>
        <div className="h-4 bg-foreground/10 rounded w-full"></div>
        <div className="h-4 bg-foreground/10 rounded w-3/4"></div>
        <div className="h-4 bg-foreground/10 rounded w-full"></div>
        <div className="h-4 bg-foreground/10 rounded w-2/3"></div>
      </div>
    </div>
  );
}

async function BlogPostContent({ path }: { path: string }) {
  const post = await fetchBlogPost(path);

  if (!post) {
    return (
      <div className="text-center py-12">
        <h1 className="text-2xl font-bold mb-4 text-foreground">Article non trouvé</h1>
        <p className="text-foreground/60 mb-4">
          L'article demandé n'existe pas ou n'a pas pu être chargé.
        </p>
        <Link 
          href="/blog" 
          className="text-foreground/80 hover:text-foreground transition-colors underline decoration-1 underline-offset-4"
        >
          Retour au blog
        </Link>
      </div>
    );
  }

  return (
    <article className="max-w-4xl mx-auto">
      <header className="mb-8">
        <div className="flex items-center gap-4 mb-4 text-sm text-foreground/60">
          <span className="bg-foreground/5 px-2 py-1 rounded">
            {post.category}
          </span>
          <time dateTime={post.date}>{post.date}</time>
        </div>
        <h1 className="text-3xl font-bold mb-4 text-foreground">
          {post.title}
        </h1>
      </header>
      
      <div className="prose prose-lg max-w-none">
        <MarkdownRenderer content={post.content} />
      </div>
      
      <footer className="mt-12 pt-8 border-t border-foreground/10">
        <div className="flex items-center justify-between text-sm text-foreground/60">
          <span>Chemin: {post.path}</span>
          <span>Dernière mise à jour: {post.date}</span>
        </div>
      </footer>
    </article>
  );
}

export default async function BlogPostPage({ searchParams }: PageProps) {
  const resolvedParams = await searchParams;
  const path = resolvedParams.path;

  if (!path) {
    notFound();
  }

  return (
    <div className="pb-16 px-4 mx-auto max-w-6xl">
      <header className="mb-8">
        <div className="flex items-center gap-4 mb-4">
          <Link 
            href="/blog" 
            className="text-foreground/60 hover:text-foreground transition-colors text-sm underline decoration-1 underline-offset-4"
          >
            ← Retour au blog
          </Link>
          <Link 
            href="/" 
            className="text-foreground/60 hover:text-foreground transition-colors text-sm underline decoration-1 underline-offset-4"
          >
            Accueil
          </Link>
        </div>
      </header>
      
      <main>
        <Suspense fallback={<LoadingSkeleton />}>
          <BlogPostContent path={path} />
        </Suspense>
      </main>
    </div>
  );
}
